* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

a {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.card {
  border: 1px solid #f3f3f3;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
}

.card__pictures {
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.card__name {
  width: 100%;
  margin: 0;
}

.card__name-name {
  text-align: left;
  color: #060b35;
  margin: 36px 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__name-code {
  text-align: left;
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.card__review-rewiews {
  text-align: right;
  align-self: end;
  width: 100%;
  padding-left: 17px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  margin: 24px 0 16px;
  display: flex;
}

.card__price-let {
  text-align: left;
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-num {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  outline: none;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
}

.card__button-text {
  color: #fff;
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__button:hover .card__button-text {
  color: #00acdc;
}

.stars {
  width: 112px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
